@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.text-area {
  max-width: 100%;
  word-wrap: break-word;
  width: 860px;
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 200px;
}
.app-body,
.mobile-top-bar {
  display: flex;
  outline: none;
  flex-direction: row;
}

.wrapper {
  position: fixed;
  padding: 0;
  margin: 0;
  overflow: auto;

  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
}

.app-navigation-white {
  padding: 5px 0px 5px 30px;
  text-align: left;
  font-family: "Georgia";
  line-height: 2.1;
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  width: 150px;
}

.app-navigation-black {
  padding: 5px 0px 5px 30px;
  position: relative;
  text-align: left;
  font-family: "Georgia";
  line-height: 2.1;
  text-decoration: none;
  color: #000;
  font-size: 18px;
  width: 150px;
}

.app-password {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}
.pages-title {
  font-family: "Georgia";
  color: #000;
  font-size: 20px;
}

.pages-line {
  width: 100px;
  border-top: 0.8px solid #aaa;
  margin-top: 10px;
  padding-bottom: 10px;
}

.app-side-bar {
  padding: 40px;
  width: 250px;
  height: auto;
  display: flex;
  flex-direction: column;
}
p {
  margin-bottom: 6.55%;
  position: relative;
  text-align: left;
  font-family: "Georgia";
  line-height: 2.1;
  color: #000;
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 0.1rem;
  padding: 10px 0;
  width: 97.53%;
}
.main_content {
  width: 860px;
  margin-bottom: 100px;
  max-width: 100%;
  padding: 20px;
  margin: 10px;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  font-family: "PT Sans";
  font-size: 18px;
}
.tiles {
  padding: 6vh 0vw;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  position: relative;
}

.bm-burger-button {
  outline: none;
  position: fixed;
  width: 36px;
  height: 30px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.container {
  position: relative;
  padding: 20px;

  cursor: pointer;
  overflow: hidden;
}

.image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.middle {
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.container:hover .image {
  opacity: 0.3;
}

.container:hover .middle {
  opacity: 1;
}

.text {
  background-color: #4caf50;
  color: white;
  font-size: 16px;
  padding: 16px 32px;
}

